import { Component, h, Prop, Element } from '@stencil/core';

@Component({
  tag: 'ks-order-address-field',
})
export class OrderAddressField {
    @Element() root: HTMLElement;
    @Prop() name: string;

    componentDidLoad() {
        this.root.style.display = "block";
    }

    render() {


        return[
            <span innerHTML={this.name}></span>,
            <div class="uk-padding-small uk-margin-bottom" style={{backgroundColor: "#f2f2f2"}}>
                <slot />
            </div>
        ];
    }
}